import common from "../../common/common.js";
// 导入自定义组件
import helloComp from "../../components/helloComp.vue";
import trailerStars from "../../components/trailerStars.vue";
export default {
	data() {
		return {
			title: 'Hello',
			carouselList:[],
			hotSuperheroList:[],
			hotTrailerList:[],
			
			animationData:{}
		}
	},
	onUnload(){
		// 页面卸载的时候，清除动画数据
		this.animationData = {};
	},
	onLoad() {
		var me = this;
		
		// 在页面创建的时候，创建一个临时的动画对象
		this.animation = uni.createAnimation();
		// 获取common.js中的服务器地址
		// var serverUrl = common.serverUrl;
		// 通过挂载到main.js中获取服务器的地址，作为全局变量
		var serverUrl = me.serverUrl;
		//请求轮播数据
		uni.request({
			url: serverUrl + '/index/carousel/list',
			method: "POST",
			header:{
				'content-type':'application/x-www-form-urlencoded'
			},
			data:{
				qq:'466481615'
			},
			success:(res) => {
				console.log(res.data);
				//获取真实数据之前，判断状态是否为200
				if(res.data.status == 200 ){
					var carouselList = res.data.data;
					this.carouselList = carouselList;
				}
				
				this.text = 'request success';
			}
		});
		
		// 查询超英热门
		uni.request({
			url: serverUrl + '/index/movie/hot?type=superhero',
			method: "POST",
			header:{
				'content-type':'application/x-www-form-urlencoded'
			},
			data:{
				qq:'466481615'
			},
			success:(res) => {
				//console.log(res.data);
				//获取真实数据之前，判断状态是否为200
				if(res.data.status == 200 ){
					var hotSuperheroList = res.data.data;
					this.hotSuperheroList = hotSuperheroList;
				}
				
				// this.text = 'request success';
			}
		});
		
		// 热门预告
		uni.request({
			url: serverUrl + '/index/movie/hot?type=trailer',
			method: "POST",
			header:{
				'content-type':'application/x-www-form-urlencoded'
			},
			data:{
				qq:'466481615'
			},
			success:(res) => {
				//console.log(res.data);
				//获取真实数据之前，判断状态是否为200
				// debugger;
				if(res.data.status == 200 ){
					var hotTrailerList = res.data.data;
					this.hotTrailerList = hotTrailerList;
				}
				
				// this.text = 'request success';
			}
		})
	},
	methods: {
		// 点赞动画效果
		praiseMe(){	
			// 构建动画数据，并且通过step来表示这组动画的完成
			this.animation.translateY(-60).opacity(1).step({
				duration:400
			});
			
			//导出动画数据到view组件，实现效果
			this.animationData = this.animation.export();
			
			//还原动画
			setTimeout(function(){
				this.animation.translateY(0).opacity(0).step({
				duration:0
				});
				this.animationData = this.animation.export();
			}.bind(this),500)
		}
	},
	
	components:{
		helloComp,
		trailerStars
	}
}
